body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;min-width:320px;box-sizing:border-box;background:#ebebeb}code,input,body,button{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}html,body,#root{height:100%}

input:focus,input:active{outline:none}

.screen-component{text-align:right;background:#fff;padding:5px 15px}.screen-component input{width:100%;text-align:right;box-sizing:border-box;border:none;font-size:24px}.screen-component-result{opacity:0.5;font-size:14px;min-height:20px;line-height:20px;display:flex;justify-content:space-between;flex-wrap:wrap}.screen-component-result--expression{max-width:250px;overflow:hidden;white-space:nowrap;text-overflow:ellipsis;margin-left:auto}.screen-component-result--expression.expand{cursor:pointer;-webkit-text-decoration:dotted;text-decoration:dotted;color:#318bbc}.screen-component-result--result span{padding:0 5px 0 2px}

.grid-cell-component{flex-grow:1;flex-shrink:0;flex-basis:20%;box-sizing:border-box}

.grid-component{display:flex;justify-content:space-between;flex-wrap:wrap}

button:focus,button:active{outline:none}

.keyboard-component{overflow:hidden;margin:-1px;border:#9fcde6 1px solid;box-sizing:border-box}.keyboard-component button{width:100%;display:block;background:#f0f7fb;padding:15px 5px;border:#b4d8eb 1px solid;cursor:pointer;color:#3a99cc;font-size:18px}.keyboard-component button:hover{background:#dcedf6}.keyboard-component .grid-cell-component{margin:-1px}.keyboard-component .grid-cell-component.light-blue button{background:#8bc3e1;color:#fff}.keyboard-component .grid-cell-component.light-blue button:hover{background:#63aed6}.keyboard-component .grid-cell-component.blue button{background:#63aed6;color:#fff}.keyboard-component .grid-cell-component.blue button:hover{background:#449ecf}.keyboard-component .grid-cell-component.green button{background:#8ed25d;color:#fff}.keyboard-component .grid-cell-component.green button:hover{background:#73c637}

.calculator-component{border:#d2d2d2 4px solid;background:#fff;border-radius:4px;box-sizing:border-box}

.app{height:100%;text-align:center}.app>*{display:inline-block;vertical-align:middle}.app:before{display:inline-block;vertical-align:middle;content:" ";width:0;height:100%}

